module.exports = {
	

		// our global variables for managing state
		languageCode: null,
		region: null,
		//accessid: null,
		//secretkey: null,
		sampleRate: null,
		transcription: null,
		socket: null,
		micStream: null,
		socketError: false,
		transcribeException: false,
		active: false,
		expiretime: 900,
		theparent: 'poodll',
		owner: 'poodll',
		appid: 'poodll',
		presignedURL: false,
	
		init: function(opts){

			this.audioUtils= require('./audioUtils');  // for encoding audio data as PCM
			this.crypto= require('crypto'); // tot sign our pre-signed URL
			this.v4 = require('./aws-signature-v4'); // to generate our pre-signed URL
			this.marshaller=  require("@aws-sdk/eventstream-marshaller"); // for converting binary event stream messages to and from JSON
			this.util_utf8_node= require("@aws-sdk/util-utf8-node"); // utilities for encoding and decoding UTF8
			this.mic= require('microphone-stream'); // collect microphone input as a stream of raw bytes
			// our converter between binary event streams messages and JSON
			this.eventStreamMarshaller= new this.marshaller.EventStreamMarshaller(this.util_utf8_node.toUtf8, this.util_utf8_node.fromUtf8);

			// set the language and region from the dropdowns
			this.languageCode=opts['language'];
			this.region=opts['region'];
			this.token=opts['token'];
			this.expiretime=opts['expiretime'];
			this.owner=opts['owner'];
			this.appid=opts['appid'];
			this.theparent=opts['parent'];
			this.wsserver="https://cloud.poodll.com";
			//this.accessid=opts['accessid'];
			//this.secretkey=opts['secretkey'];			
			
			if (this.languageCode == "en-US" || this.languageCode == "es-US"){
				this.sampleRate = 44100;
			}else{
				this.sampleRate = 8000;
			}
			
			//fetch a presignedURL on load so we are good to begin
			this.fetchPresignedURL();
	
			//to kick off .. pass stream to start ()
			//(in promise from getusermedia is good)
		
			//when you finish call
			//closeSocket();
		},
	
		onPartialResult: function(transcript,results){
			//override me
			console.log('PartialResult:' + transcript);
		},
	
		onFinalResult: function(transcript,results){
			//override me
			console.log('FinalResult:' + transcript);
		},	
	
		onError: function(message) {
		     //override me
			console.log('Error:' + message);
		},
		
		fetchPresignedURL: function(){
		 //The REST API we are calling
            var functionname = 'local_cpapi_fetch_streamingtranscriber';

            //fetch the Posturl. We need this.
            //set up our ajax request
            var xhr = new XMLHttpRequest();
            var that = this;

            //set up our handler for the response
            xhr.onreadystatechange = function (e) {
                if (this.readyState === 4) {
                    if (xhr.status == 200) {

                        //get a yes or forgetit or tryagain
                        var payload = xhr.responseText;
                        var payloadobject = JSON.parse(payload);
                        if (payloadobject) {

                            //returnCode > 0  indicates an error
                            if (payloadobject.returnCode > 0) {
                                that.onError('error:' + payloadobject.returnMessage);
                                return;
                             //if all good, then lets do the embed
                            } else {
                                that.presignedURL = payloadobject.streamingURL;                            }
                        } else {
                            that.onError('error:' + payloadobject.message);

                        }
                    } else {
                        that.onError('Not 200 response:' + xhr.status);
                    }
                }
            };

            //log.debug(params);
            var xhrparams = "wstoken=" + this.token
                + "&wsfunction=" + functionname
                + "&moodlewsrestformat=json"
                + '&parent=' + this.theparent
                + '&appid=' + this.appid
                + '&owner=' + this.owner
                + '&region=' + this.region
                + '&expiretime=' + this.expiretime
                + '&languagecode=' + this.languageCode
                + '&samplerate=' + this.sampleRate;

            var serverurl = this.wsserver + "/webservice/rest/server.php";
            xhr.open("POST", serverurl, true);
            xhr.setRequestHeader("Cache-Control", "no-cache");
            xhr.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
            xhr.send(xhrparams);
		
		},

		start: function(userMediaStream, transcriber) {
			var that = transcriber;
			
			//do a little sanity check on our websocket
			//if its already open just return
			if(that.socket != null && (that.socket.readyState === 0 || that.socket.readyState === 1)){
				that.onError('WebSocket already open or opening');
				return;
			}
	
			//let's get the mic input from the browser, via the microphone-stream module
			that.micStream = new that.mic();
			that.micStream.setStream(userMediaStream);
			

			// Pre-signed URLs are a way to authenticate a request (or WebSocket connection, in this case)
			// via Query Parameters. Learn more: https://docs.aws.amazon.com/AmazonS3/latest/API/sigv4-query-string-auth.html
			//var url = that.createPresignedUrl();

			//here we use our pre fetched presignedURL and fetch another one right away in case we need to do this more than once
			if(that.presignedURL){
			  var url = that.presignedURL;
			  that.presignedURL =false;  
			  that.fetchPresignedURL(); 
			}else{
				that.onError('we have no presigned URL. giving up');
				return;
			}

			

			//open up our WebSocket connection
			that.socket = new WebSocket(url);
			that.socket.binaryType = "arraybuffer";

			// when we get audio data from the mic, send it to the WebSocket if possible
			that.socket.onopen = function() {
				that.micStream.on('data', function(rawAudioChunk) {
					// the audio stream is raw audio bytes. Transcribe expects PCM with additional metadata, encoded as binary
					var binary = that.convertAudioToBinaryMessage(rawAudioChunk);

					if (that.socket.OPEN)
						that.socket.send(binary);
				}
			)};

			// handle messages, errors, and close events
			that.wireSocketEvents();
			
			//set active flag
			that.active=true;
		},

		

		wireSocketEvents: function() {
			var that = this;
			// handle inbound messages from Amazon Transcribe
			this.socket.onmessage = function (message) {
				//convert the binary event stream message to JSON
				var messageWrapper = that.eventStreamMarshaller.unmarshall(Buffer(message.data));
				var messageBody = JSON.parse(String.fromCharCode.apply(String, messageWrapper.body));
				if (messageWrapper.headers[":message-type"].value === "event") {
					that.handleEventStreamMessage(messageBody);
				}
				else {
					that.transcribeException = true;
					that.onError(messageBody.Message);
				}
			};

			this.socket.onerror = function () {
				that.socketError = true;
				that.onError('WebSocket connection error. Try again.');
			};
	
			this.socket.onclose = function (closeEvent) {
				that.active=false;
				that.micStream.stop();
		
				// the close event immediately follows the error event; only handle one.
				if (!that.socketError && !that.transcribeException) {
					if (closeEvent.code != 1000) {
						that.onError('Streaming Exception:' + closeEvent.reason);
					}
				}
			};
		},

		handleEventStreamMessage: function (messageJson) {
			var results = messageJson.Transcript.Results;

			if (results.length > 0) {
				if (results[0].Alternatives.length > 0) {
					var transcript = results[0].Alternatives[0].Transcript;

					// fix encoding for accented characters
					transcript = decodeURIComponent(escape(transcript));

					// update the textarea with the latest result
					this.onPartialResult(transcript,results);

					// if this transcript segment is final, add it to the overall transcription
					if (!results[0].IsPartial) {
						//this.transcription += transcript + "\n";
						this.onFinalResult(transcript,results);
					}
				}
			}
		},

		closeSocket: function () {
     		this.active=false;
			if (this.socket != null && this.socket.readyState == 1) {
				this.micStream.stop();

				// Send an empty frame so that Transcribe initiates a closure of the WebSocket after submitting all transcripts
				var emptyMessage = this.getAudioEventMessage(Buffer.from(new Buffer([])));
				var emptyBuffer = this.eventStreamMarshaller.marshall(emptyMessage);
				this.socket.send(emptyBuffer);
			}
		},

		convertAudioToBinaryMessage: function (audioChunk) {
			var raw = this.mic.toRaw(audioChunk);

			if (raw == null)
				return;

			// downsample and convert the raw audio bytes to PCM
			var downsampledBuffer = this.audioUtils.downsampleBuffer(raw, this.sampleRate);
			var pcmEncodedBuffer = this.audioUtils.pcmEncode(downsampledBuffer);

			// add the right JSON headers and structure to the message
			var audioEventMessage = this.getAudioEventMessage(Buffer.from(pcmEncodedBuffer));

			//convert the JSON object + headers into a binary event stream message
			var binary = this.eventStreamMarshaller.marshall(audioEventMessage);

			return binary;
		},

		getAudioEventMessage: function(buffer) {
			// wrap the audio data in a JSON envelope
			return {
				headers: {
					':message-type': {
						type: 'string',
						value: 'event'
					},
					':event-type': {
						type: 'string',
						value: 'AudioEvent'
					}
				},
				body: buffer
			};
		},

		createPresignedUrl: function() {
			var endpoint = "transcribestreaming." + this.region + ".amazonaws.com:8443";

			// get a preauthenticated URL that we can use to establish our WebSocket
			return this.v4.createPresignedURL(
				'GET',
				endpoint,
				'/stream-transcription-websocket',
				'transcribe',
				this.crypto.createHash('sha256').update('', 'utf8').digest('hex'), {
					'key': this.accessid,
					'secret': this.secretkey,
					'sessionToken': '',
					'protocol': 'wss',
					'expires': 15,
					'region': this.region,
					'query': "language-code=" + this.languageCode + "&media-encoding=pcm&sample-rate=" + this.sampleRate
				}
			);
		},
	}
